.media-title {
  margin-bottom: 70px; /* Espace sous le titre */
}
.scroll-section {
  padding: 20px;
  text-align: center;
}
.media-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 10px;
}
.media-item {
  position: relative;
  overflow: hidden;
  cursor: pointer; /* Change le curseur en une main pour les éléments cliquables */
}
.media-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}
.media-item:hover img {
  transform: scale(1.1);
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent; /* Changez la couleur de fond à transparent */
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.popup-content {
  max-width: 90%;
  max-height: 90%;
}
.popup-content img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}